import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, "#em_photo{height:40px;width:40px}", "",{"version":3,"sources":["webpack://./resources/js/components/product/index.vue"],"names":[],"mappings":"AAiJA,UACA,WAAA,CACA,UACA","sourcesContent":[" \r\n\r\n<template>\r\n  \r\n    <div>\r\n  \r\n   <div class=\"row\">\r\n    <router-link to=\"/store-product\" class=\"btn btn-primary\">Add Product </router-link>\r\n     \r\n   </div>\r\n  <br>\r\n     <input type=\"text\" v-model=\"searchTerm\" class=\"form-control\" style=\"width: 300px;\" placeholder=\"Search Here\">\r\n  \r\n  \r\n  <br>\r\n  \r\n     <div class=\"row\">\r\n              <div class=\"col-lg-12 mb-4\">\r\n                <!-- Simple Tables -->\r\n                <div class=\"card\">\r\n                  <div class=\"card-header py-3 d-flex flex-row align-items-center justify-content-between\">\r\n                    <h6 class=\"m-0 font-weight-bold text-primary\">Product List</h6>\r\n                  </div>\r\n                  <div class=\"table-responsive\">\r\n                    <table class=\"table align-items-center table-flush\">\r\n                      <thead class=\"thead-light\">\r\n                        <tr>\r\n                          <th>Name</th>\r\n                          <th>Code</th>\r\n                          <th>Photo</th>\r\n                          <th>Category</th>\r\n                          <th>Buying Price</th>\r\n                          <th>Selling Price</th>\r\n                          <th>Root</th>\r\n                          <th>Action</th>\r\n                        </tr>\r\n                      </thead>\r\n                      <tbody>\r\n                        <tr v-for=\"product in filtersearch\" :key=\"product.id\">\r\n                          <td> {{ product.product_name }} </td>\r\n                          <td> {{ product.product_code }} </td>\r\n                          <td><img :src=\"product.image\" id=\"em_photo\"></td>\r\n                          <td>{{ product.category_name }}</td>\r\n                          <td>{{ product.buying_price }}</td>\r\n                          <td>{{ product.selling_price }}</td>\r\n                           <td>{{ product.root }}</td>\r\n              <td>\r\n     <router-link :to=\"{name: 'edit-product', params:{id:product.id}}\" class=\"btn btn-sm btn-primary\">Edit</router-link>\r\n  \r\n   <a @click=\"deleteProduct(product.id)\" class=\"btn btn-sm btn-danger\"><font color=\"#ffffff\">Delete</font></a>\r\n              </td>\r\n                        </tr>\r\n                      \r\n                      </tbody>\r\n                    </table>\r\n                  </div>\r\n                  <div class=\"card-footer\"></div>\r\n                </div>\r\n              </div>\r\n            </div>\r\n            <!--Row-->\r\n  \r\n  \r\n     \r\n    </div>\r\n  \r\n  \r\n  </template>\r\n  \r\n  \r\n  \r\n  <script type=\"text/javascript\">\r\n    \r\n    export default {\r\n      created(){\r\n        if (!User.loggedIn()) {\r\n          this.$router.push({name: '/'})\r\n        }\r\n      },\r\n      data(){\r\n        return{\r\n          products:[],\r\n          searchTerm:''\r\n        }\r\n      },\r\n      computed:{\r\n        filtersearch(){\r\n        return this.products.filter(product => {\r\n           return product.product_name.match(this.searchTerm)\r\n        }) \r\n        }\r\n      },\r\n   \r\n    methods:{\r\n      allProduct(){\r\n        axios.get('/api/product/')\r\n        .then(({data}) => (this.products = data))\r\n        .catch()\r\n      },\r\n    deleteProduct(id){\r\n               Swal.fire({\r\n                title: 'Are you sure?',\r\n                text: \"You won't be able to revert this!\",\r\n                icon: 'warning',\r\n                showCancelButton: true,\r\n                confirmButtonColor: '#3085d6',\r\n                cancelButtonColor: '#d33',\r\n                confirmButtonText: 'Yes, delete it!'\r\n              }).then((result) => {\r\n                if (result.value) {\r\n                  axios.delete('/api/product/'+id)\r\n                 .then(() => {\r\n                  this.products = this.products.filter(product => {\r\n                    return product.id != id\r\n                  })\r\n                 })\r\n                 .catch(() => {\r\n                  this.$router.push({name: 'product'})\r\n                 })\r\n                  Swal.fire(\r\n                    'Deleted!',\r\n                    'Your file has been deleted.',\r\n                    'success'\r\n                  )\r\n                }\r\n              })\r\n  \r\n    } \r\n  \r\n    },\r\n    created(){\r\n      this.allProduct();\r\n    } \r\n    \r\n  \r\n    } \r\n  </script>\r\n  \r\n  \r\n  <style type=\"text/css\">\r\n    #em_photo{\r\n      height: 40px;\r\n      width: 40px;\r\n    }\r\n  </style>"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
