___CSS_LOADER_EXPORT___.push([module.id, "#em_photo{height:40px;width:40px}", "",{"version":3,"sources":["webpack://./resources/js/components/employee/index.vue"],"names":[],"mappings":"AAgIA,UACA,WAAA,CACA,UACA","sourcesContent":["<template>  \r\n  <div>\r\n   <div class=\"row\">\r\n    <router-link to=\"/store-employee\" class=\"btn btn-primary\">Add Employee </router-link>\r\n   </div>\r\n    <br>\r\n     <input type=\"text\" v-model=\"searchTerm\" class=\"form-control\" style=\"width: 300px;\" placeholder=\"Search Here\">\r\n    <br>\r\n    <div class=\"row\">\r\n      <div class=\"col-lg-12 mb-4\">\r\n        <!-- Simple Tables -->\r\n        <div class=\"card\">\r\n          <div class=\"card-header py-3 d-flex flex-row align-items-center justify-content-between\">\r\n            <h6 class=\"m-0 font-weight-bold text-primary\">Employee List</h6>\r\n          </div>\r\n          <div class=\"table-responsive\">\r\n            <table class=\"table align-items-center table-flush\">\r\n              <thead class=\"thead-light\">\r\n                <tr>\r\n                  <th>Name</th>\r\n                  <th>Photo</th>\r\n                  <th>Phone</th>\r\n                  <th>Sallery</th>\r\n                  <th>Joining Date</th>\r\n                  <th>Action</th>\r\n                </tr>\r\n              </thead>\r\n              <tbody>\r\n                <tr v-for=\"employee in filtersearch\" :key=\"employee.id\">\r\n                  <td> {{ employee.name }} </td>\r\n                  <td><img :src=\"employee.photo\" id=\"em_photo\"></td>\r\n                  <td>{{ employee.phone }}</td>\r\n                  <td>{{ employee.sallery }}</td>\r\n                  <td>{{ employee.joining_date }}</td>\r\n                  <td>\r\n                    <router-link :to=\"{name: 'edit-employee', params:{id:employee.id}}\" class=\"btn btn-sm btn-primary\">Edit</router-link>\r\n                    <a @click=\"deleteEmployee(employee.id)\" class=\"btn btn-sm btn-danger\"><font color=\"#ffffff\">Delete</font></a>\r\n                  </td>\r\n                </tr>\r\n              </tbody>\r\n            </table>\r\n          </div>\r\n          <div class=\"card-footer\"></div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n    <!--Row-->\r\n  </div>\r\n  </template>\r\n\r\n  <script>\r\n    export default {\r\n      mounted() {\r\n        this.checkAuth();\r\n      },\r\n      data(){\r\n        return{\r\n          employees:[],\r\n          searchTerm:'',\r\n          isAuthenticated: false,\r\n        }\r\n      },\r\n      computed:{\r\n        filtersearch(){\r\n        return this.employees.filter(employee => {\r\n           return employee.name.match(this.searchTerm)\r\n        }) \r\n        }\r\n      },\r\n      methods:{\r\n        allEmployee(){\r\n          axios.get('/api/employee/')\r\n          .then(({data}) => (this.employees = data))\r\n          .catch()\r\n        },\r\n        deleteEmployee(id){\r\n            Swal.fire({\r\n              title: 'Are you sure?',\r\n              text: \"You won't be able to revert this!\",\r\n              icon: 'warning',\r\n              showCancelButton: true,\r\n              confirmButtonColor: '#3085d6',\r\n              cancelButtonColor: '#d33',\r\n              confirmButtonText: 'Yes, delete it!'\r\n            }).then((result) => {\r\n              if (result.value) {\r\n                axios.delete('/api/employee/'+id)\r\n              .then(() => {\r\n                this.employees = this.employees.filter(employee => {\r\n                  return employee.id != id\r\n                })\r\n              })\r\n              .catch(() => {\r\n                this.$router.push({name: 'employee'})\r\n              })\r\n                Swal.fire(\r\n                  'Deleted!',\r\n                  'Your file has been deleted.',\r\n                  'success'\r\n                )\r\n              }\r\n            })\r\n        },\r\n        checkAuth() {\r\n        axios.get('/api/employee')\r\n            .then(response => {\r\n                // User is authenticated\r\n                this.isAuthenticated = true;\r\n            })\r\n            .catch(error => {\r\n                // User is not authenticated\r\n                this.isAuthenticated = false;\r\n            });\r\n      }, \r\n      },\r\n      created(){\r\n        this.allEmployee();\r\n      } \r\n    } \r\n</script>\r\n  \r\n<style type=\"text/css\">\r\n  #em_photo{\r\n    height: 40px;\r\n    width: 40px;\r\n  }\r\n</style>\r\n  "],"sourceRoot":""}]);
