___CSS_LOADER_EXPORT___.push([module.id, "#em_photo{height:40px;width:40px}", "",{"version":3,"sources":["webpack://./resources/js/components/product/stock.vue"],"names":[],"mappings":"AAoHA,UACA,WAAA,CACA,UACA","sourcesContent":[" \r\n\r\n<template>\r\n  \r\n  <div>\r\n\r\n <div class=\"row\">\r\n  <router-link to=\"/store-product\" class=\"btn btn-primary\">Add Product </router-link>\r\n   \r\n </div>\r\n<br>\r\n   <input type=\"text\" v-model=\"searchTerm\" class=\"form-control\" style=\"width: 300px;\" placeholder=\"Search Here\">\r\n\r\n\r\n<br>\r\n\r\n   <div class=\"row\">\r\n            <div class=\"col-lg-12 mb-4\">\r\n              <!-- Simple Tables -->\r\n              <div class=\"card\">\r\n                <div class=\"card-header py-3 d-flex flex-row align-items-center justify-content-between\">\r\n                  <h6 class=\"m-0 font-weight-bold text-primary\">Stock</h6>\r\n                </div>\r\n                <div class=\"table-responsive\">\r\n                  <table class=\"table align-items-center table-flush\">\r\n                    <thead class=\"thead-light\">\r\n                      <tr>\r\n                        <th>Name</th>\r\n                        <th>Code</th>\r\n                        <th>Photo</th>\r\n                        <th>Category</th>\r\n                        <th>Buying Price</th>\r\n                        <th>Status</th>\r\n                        <th>Quantity</th>\r\n                        <th>Action</th>\r\n                      </tr>\r\n                    </thead>\r\n                    <tbody>\r\n                      <tr v-for=\"product in filtersearch\" :key=\"product.id\">\r\n                        <td> {{ product.product_name }} </td>\r\n                        <td> {{ product.product_code }} </td>\r\n                        <td><img :src=\"product.image\" id=\"em_photo\"></td>\r\n                        <td>{{ product.category_name }}</td>\r\n                        <td>{{ product.buying_price }}</td>\r\n  <td v-if=\"product.product_quantity  >= 1 \"><span class=\"badge badge-success\">Available </span></td>\r\n   <td v-else><span class=\"badge badge-danger\">Stock Out </span></td>\r\n                         <td>{{ product.product_quantity }}</td>\r\n            <td>\r\n   <router-link :to=\"{name: 'edit-stock', params:{id:product.id}}\" class=\"btn btn-sm btn-primary\">Edit</router-link>\r\n \r\n            </td>\r\n                      </tr>\r\n                    \r\n                    </tbody>\r\n                  </table>\r\n                </div>\r\n                <div class=\"card-footer\"></div>\r\n              </div>\r\n            </div>\r\n          </div>\r\n          <!--Row-->\r\n\r\n\r\n   \r\n  </div>\r\n\r\n\r\n</template>\r\n\r\n\r\n\r\n<script type=\"text/javascript\">\r\n  \r\n  export default {\r\n    created(){\r\n      if (!User.loggedIn()) {\r\n        this.$router.push({name: '/'})\r\n      }\r\n    },\r\n    data(){\r\n      return{\r\n        products:[],\r\n        searchTerm:''\r\n      }\r\n    },\r\n    computed:{\r\n      filtersearch(){\r\n      return this.products.filter(product => {\r\n         return product.product_name.match(this.searchTerm)\r\n      }) \r\n      }\r\n    },\r\n \r\n  methods:{\r\n    allProduct(){\r\n      axios.get('/api/product/')\r\n      .then(({data}) => (this.products = data))\r\n      .catch()\r\n    }   \r\n\r\n  },\r\n  created(){\r\n    this.allProduct();\r\n  } \r\n  \r\n\r\n  } \r\n</script>\r\n\r\n\r\n<style type=\"text/css\">\r\n  #em_photo{\r\n    height: 40px;\r\n    width: 40px;\r\n  }\r\n</style>"],"sourceRoot":""}]);
