___CSS_LOADER_EXPORT___.push([module.id, "#em_photo{height:40px;width:40px}", "",{"version":3,"sources":["webpack://./resources/js/components/order/order.vue"],"names":[],"mappings":"AA+GA,UACA,WAAA,CACA,UACA","sourcesContent":["   \r\n\r\n<template>\r\n  \r\n    <div>\r\n  \r\n   \r\n  <br>\r\n     <input type=\"text\" v-model=\"searchTerm\" class=\"form-control\" style=\"width: 300px;\" placeholder=\"Search Here\">\r\n  \r\n  \r\n  <br>\r\n  \r\n     <div class=\"row\">\r\n              <div class=\"col-lg-12 mb-4\">\r\n                <!-- Simple Tables -->\r\n                <div class=\"card\">\r\n                  <div class=\"card-header py-3 d-flex flex-row align-items-center justify-content-between\">\r\n                    <h6 class=\"m-0 font-weight-bold text-primary\">Today Orders</h6>\r\n                  </div>\r\n                  <div class=\"table-responsive\">\r\n                    <table class=\"table align-items-center table-flush\">\r\n                      <thead class=\"thead-light\">\r\n                        <tr>\r\n                          <th>Name</th>\r\n                          <th>Total Amount </th>\r\n                          <th>Pay</th>\r\n                          <th>Due</th>\r\n                          <th>PayBy</th>\r\n                           <th>Action</th>\r\n                        </tr>\r\n                      </thead>\r\n                      <tbody>\r\n                        <tr v-for=\"order in filtersearch\" :key=\"order.id\">\r\n                          <td> {{ order.name }} </td>\r\n                          <td> {{ order.total }} $ </td>\r\n                          <td> {{ order.pay }} $ </td>\r\n                          <td> {{ order.due }} $  </td>\r\n                          <td> {{ order.payby }} </td>\r\n                         \r\n              <td>\r\n     <router-link :to=\"{name: 'view-order', params:{id:order.id}}\" class=\"btn btn-sm btn-primary\">View</router-link>\r\n  \r\n   \r\n              </td>\r\n                        </tr>\r\n                      \r\n                      </tbody>\r\n                    </table>\r\n                  </div>\r\n                  <div class=\"card-footer\"></div>\r\n                </div>\r\n              </div>\r\n            </div>\r\n            <!--Row-->\r\n  \r\n  \r\n     \r\n    </div>\r\n  \r\n  \r\n  </template>\r\n  \r\n  \r\n  \r\n  <script type=\"text/javascript\">\r\n    \r\n    export default {\r\n      created(){\r\n        if (!User.loggedIn()) {\r\n          this.$router.push({name: '/'})\r\n        }\r\n      },\r\n      data(){\r\n        return{\r\n          orders:[],\r\n          searchTerm:''\r\n        }\r\n      },\r\n      computed:{\r\n        filtersearch(){\r\n        return this.orders.filter(order => {\r\n           return order.name.match(this.searchTerm)\r\n        }) \r\n        }\r\n      },\r\n   \r\n    methods:{\r\n      allOrder(){\r\n        axios.get('/api/orders/')\r\n        .then(({data}) => (this.orders = data))\r\n        .catch()\r\n      },\r\n     \r\n  \r\n    },\r\n    created(){\r\n      this.allOrder();\r\n    } \r\n    \r\n  \r\n    } \r\n  </script>\r\n  \r\n  \r\n  <style type=\"text/css\">\r\n    #em_photo{\r\n      height: 40px;\r\n      width: 40px;\r\n    }\r\n  </style>"],"sourceRoot":""}]);
